.landingContainer{
    background-color: black;
    max-width: 1920px;
    margin: 0 auto;
  }
  
.landing{
    display: grid;
    grid-template-rows: 1fr;
    grid-template-columns: 1fr 1fr;
    grid-template-areas: 
    "image titleGrid";

    background-color: black;
    margin: 0 auto;
}

#imageContainer{
    grid-area: image;
}

#mainImage{
    display: block;
    margin: 0 auto;
    width: 60%;

    opacity: 0;
    -webkit-animation-name: imageFadeIn;
            animation-name: imageFadeIn;
    -webkit-animation-duration: 7s;
            animation-duration: 7s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-animation-delay: 1s;
            animation-delay: 1s;
}

@-webkit-keyframes imageFadeIn {
    0% {
    opacity: 0;
    }
    100% {
    opacity: 1;
    }
}

@keyframes imageFadeIn {
    0% {
    opacity: 0;
    }
    100% {
    opacity: 1;
    }
}

#titleContainer{
    grid-area: titleGrid;
    display: grid;
    grid-template-rows: 2fr 2fr 0.5fr 2fr;
    grid-template-columns: 1fr;
    grid-template-areas: 
    "."
    "."
    "main"
    "subtitle";
    color: white;
    white-space: nowrap;
}

#titleContainer h1{
    grid-area: main;
    display: inline;
    color: black;
    font-family: Arial, Helvetica, sans-serif;
    letter-spacing: 30px;
    font-weight: 100;
    font-size: 100px;
    margin: 0;

    -webkit-animation-name: fadeIn;

            animation-name: fadeIn;
    -webkit-animation-duration: 7s;
            animation-duration: 7s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;

}


#titleContainer h4{
    grid-area: subtitle;
    color: black;
    display: inline;
    letter-spacing: 16px;
    /* font-size: 30px; */
    font-family: Arial, Helvetica, sans-serif;
    font-weight: 100;
    margin: 10px 0 0 60px;

    -webkit-animation-name: slide-in;

            animation-name: slide-in;
    -webkit-animation-duration: 3s;
            animation-duration: 3s;
    -webkit-animation-delay: 1.5s;
            animation-delay: 1.5s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
}

@-webkit-keyframes slide-in {
    0% {
        margin: 10px 0 0;
        color: black;
        letter-spacing: 16px;
        font-size: 10px;
    }
    100% {
        margin-left: 10px 0 0 60px;
        color: white;
        letter-spacing: 7px;
        font-size: 30px;
    }
}

@keyframes slide-in {
    0% {
        margin: 10px 0 0;
        color: black;
        letter-spacing: 16px;
        font-size: 10px;
    }
    100% {
        margin-left: 10px 0 0 60px;
        color: white;
        letter-spacing: 7px;
        font-size: 30px;
    }
}

@-webkit-keyframes fadeIn {
    0% {
    color: black;
    letter-spacing: 16px;
    }
    100% {
    color: white;
    letter-spacing: 7px;
    }
}

@keyframes fadeIn {
    0% {
    color: black;
    letter-spacing: 16px;
    }
    100% {
    color: white;
    letter-spacing: 7px;
    }
}

@media only screen and (max-width: 1330px){
    .landing{
    grid-template-rows: 2fr 1fr;
    grid-template-columns: 1fr;

