nav, .mobile-nav-container{
    width: 85%;
    max-width: 1350px;
    margin: 0 auto;
  }

nav{
    text-align: center;
    display: grid;
    grid-template-columns: repeat(5, 1fr) ;
    grid-gap: 2em;
    /* background-color: rgba(0, 0, 0, .9); */
    padding: 1em;
    border-radius: 4px;
}

nav a{
    color: black;
    text-decoration: none;

    border: 1px solid black;
    border-radius: 4px;
    text-shadow: 0 0 0 black;
}

nav a:hover{
    -webkit-animation-name: nav-hover;
            animation-name: nav-hover;
    -webkit-animation-duration: .5s;
            animation-duration: .5s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    
}

@-webkit-keyframes nav-hover{
    from{
        letter-spacing: auto;
        color: black;
        background-color: white;
    }
    to{
        letter-spacing: .5em;
        color: white;
        background-color: rgba(0,0,0, .85);
    }
}

@keyframes nav-hover{
    from{
        letter-spacing: auto;
        color: black;
        background-color: white;
    }
    to{
        letter-spacing: .5em;
        color: white;
        background-color: rgba(0,0,0, .85);
    }
}

#mobile-nav-icon{
    display: none;
    background-image: url("https://cdn4.iconfinder.com/data/icons/wirecons-free-vector-icons/32/menu-alt-512.png");
    background-size: 100% 100%;
    height: 1.5em;
    width: 1.5em;
    max-width: 1350px;
    margin: .65em auto;

}

#mobile-menu-false, #mobile-menu-true{
    display: none;
}
  
@media only screen and (max-width: 750px){
    nav{
        display: none;
    }
    #mobile-nav-icon{
        display: block;
    }

    #mobile-menu-true{
        display: block;
    }
}




.mobile-nav-container{
    margin-bottom: .65em;
}

.mobile-menu-item{
    display: block;
    background-color: rgba(0, 0, 0, 0.85);

