}

.section-title-music{
  background-color: #00494D;
  background-color: var(--music-dark);
}

.section-title-programming{
  background-color: #540404;
  background-color: var(--programming-dark);
}

#resume-focus{
  width: 100%;
  margin: 0 auto;
  text-align: center;
  margin-bottom: 10px;
}

.resume-toggle-links{
  margin: 0 auto;
  width: 33%;
  max-width: 30em;
  min-width: 10em;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 1em;

  text-align: center;
}

@media only screen and (max-width: 1270px){
  .resume-toggle-links{
    grid-template-columns: 1fr;
  }
}

.resume-toggle-links a{
  padding: .65em;
  width: auto;

  text-decoration: none;
  border-radius: 4px;
  font-weight: 600;
  border: 1px solid black;
}

.focus-true{
  background-color: rgba(0, 0, 0, 0.5);
  color: white;
}

.focus-false{
  color: black;
}

.focus-music-true{
  background-color: #05595E;
  background-color: var(--music-light);  
}

.focus-programming-true{
  background-color: #6A1A1A;
  background-color: var(--programming-light);
}

@media only screen and (max-width: 1000px){
  .resume-box{
    grid-template-columns: 1fr;
    grid-template-rows: repeat(4, auto);
    grid-template-areas: 
    "sidebar-title"
    "intro"
    "sidebar"
    "main"
  }

  .sidebar{
    padding: 0 15% 1em;
  }

}

@media only screen and (max-width: 575px){
  .sidebar{
    padding: 0 .5em 1em;
  }
}
.contact-form{
    box-shadow: 2px 2px 10px rgb(128, 128, 128);
    padding: 1em;
}

.contact-form h3{
    margin: 0;
}

form input, textarea{
    border: -1px solid black;
    border-radius: 4px;
    font-size: 1.33em;
    margin-bottom: 1em;
}

textarea{
    width: 100%;
    resize: none;
    min-height: 10em;
    font-family: "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
    font-weight: 400;
}

form input[type=submit]{
    border-radius: 0;
    border: none;
    box-shadow: 2px 2px 10px rgb(128, 128, 128);
    background-color: rgba(0, 0, 0, 0.85);
    padding: .25em;
    color: whitesmoke;
    font-weight: 600;
    cursor: pointer;
}
